"use strict";

const path = require('path');
const process = require('process');

export class StringUtils {
    static trim(string) {
        return string.replace(/^\s+|\s+$/g, '');
    };

    /***
     * Strip a string on both ends from specific chars
     * @param string
     * @param chars
     */
    static strip(string, chars){
        const charsArray = [];
        for(let char of chars){
            switch (char){
                case "}":
                case "{":
                case "]":
                case "[":
                    charsArray.push("\\" + char);
                    break;
                default:
                    charsArray.push(char)
            }
        }
        const re = RegExp(`^[${charsArray.join("")}]+|[${charsArray.join("")}]+$`, "g");
        return string.replace(re, '');
    }
}

export function getEnv(envName, defaultValue) {
    return process.env[envName] || defaultValue;
}

export function canonizeFileName(filename) {
    return path.normalize(filename.replace(/[\\\/]/g, '/'));
}

